:root {
  --accentMain: #ffa31a;
  --accentShadow: 0 2px 25px rgba(250, 156, 16, 0.5);
  --accentMainHover: #ffba53;
  --background1: #808080;
  --background2: #292929;
  --background3: #1b1b1b;
  --background3Glass: rgba(27, 27, 27, 0.7);
  ;
  --textColor: #ffffff;
}

body {
  background-color: var(--background2);
}

path {
  fill: var(--textColor);
}

.content {
  width: calc(100% - 50px);
  margin-top: 45px;
  margin-left: 50px;
  height: 100%;
}

.contentData {
  margin-left: 20px;
  margin-bottom: 20px;
  margin-right: 20px;
}

.sidebar {
  position: fixed;
  top: 50px;
  width: 50px;
  height: 100%;
  background-color: var(--background3Glass);
  transition: width 0.3s ease;
  padding-top: 5px;
  backdrop-filter: blur(6px);
  -webkit-backdrop-filter: blur(9.2px);
}

.sidebar .sidebar-position {
  height: 50px;
  cursor: pointer;
  display: flex;
  align-items: center;
  padding: 0 12px;
}

.sidebar .sidebar-position-hover {
  /* background-color: var(--background2); */
  border-right: 5px var(--accentMain) solid;
}

.sidebar .sidebar-position:hover {
  background-color: var(--background2);
  /* border-right: 5px var(--accentMain) solid; */
}

.sidebar i {
  color: var(--accentMain);
  min-width: 25px;
  text-align: center;
}

.sidebar span {
  width: 0;
  height: 0;
  padding: 0 15px;
  transition: width 0.3s ease;
  color: var(--textColor);
  text-transform: uppercase;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.sidebar--open {
  width: 200px;
  transition: width 0.3s ease;
}

.sidebar--open .sidebar-position span {
  display: block;
  width: 100%;
  height: auto;
}


.trigger {
  cursor: pointer;
  position: fixed;
  color: var(--textColor);
  transition: width 0.3s ease;
  padding-top: 5px;
  top: 10px;
  left: 15px;
}

.trigger i:hover path {
  fill: var(--accentMain);
}

.header {
  position: fixed;
  top: 0;
  width: 100%;
  height: 50px;
  background-color: var(--background3);
}

.header h1 {
  color: var(--textColor);
  margin: 0;
  position: fixed;
  left: 70px;
  top: 10px;
}

.toggle-switch {
  position: fixed;
  width: 60px;
  top: 10px;
  right: 10px;
}

.toggle-switch label {
  position: absolute;
  width: 100%;
  height: 30px;
  background-color: var(--background2);
  border: 1px solid var(--accentMain);
  border-radius: 50px;
  cursor: pointer;
}

.toggle-switch input {
  position: absolute;
  display: none;
}

.toggle-switch .slider {
  position: absolute;
  width: 100%;
  height: 100%;
  border-radius: 50px;
  transition: 0.3s;
}

.toggle-switch input:checked~.slider {
  background-color: var(--textColor);
}

.toggle-switch .slider::before {
  content: "";
  position: absolute;
  top: 3px;
  left: 6px;
  width: 25px;
  height: 25px;
  border-radius: 50%;
  box-shadow: inset 8px -1px 0px 0px var(--textColor);
  background-color: var(--background2);
  transition: 0.3s;
}

.toggle-switch input:checked~.slider::before {
  transform: translateX(25px);
  background-color: var(--background2);
  box-shadow: none;
}

.workspaceMainInfo {
  color: var(--textColor);
}

.buttonFill {
  appearance: none;
  border: 0;
  padding: 5px 15px 5px 15px;
  height: 40px;
  color: #fff;
  background-color: var(--accentMain);
  border-radius: 5px;
  cursor: pointer;
  font-size: 15px;
}

.buttonFill:hover {
  background-color: var(--accentMainHover);
  box-shadow: var(--accentShadow);
}

.buttonWithoutFill {
  appearance: none;
  border: 0;
  padding: 5px 15px 5px 15px;
  height: 40px;
  color: var(--textColor);
  border: 1px solid var(--accentMain);
  background-color: transparent;
  border-radius: 5px;
  cursor: pointer;
  font-size: 15px;
}

.buttonWithoutFill:hover {
  background-color: var(--accentMainHover);
  box-shadow: var(--accentShadow);
}

.tableNavigationLine {
  background-color: var(--background3);
  margin-top: 10px;
  border-radius: 5px;
  width: 100%;
  cursor: pointer;
}

.tableNavigationLine p {
  padding: 5px 5px 5px 5px;
  font-size: 14px;
  color: var(--accentMain);
  font-size: 15px;
}

.tableStyle {
  border-collapse: collapse;
  width: 100%;
  border-radius: 5px;
  border: 1px solid var(--background3);
}

.tableStyle td,
.tableStyle th {
  border: 1px solid var(--background2);
  padding: 8px;
  color: var(--textColor);
}

.tableStyle tr {
  background-color: var(--background2);
}

.tableStyle tr:nth-child(even) {
  background-color: var(--background3);
  color: var(--textColor);
}

.tableStyle tr:hover {
  background-color: var(--background1);
}

.tableStyle th {
  padding-top: 12px;
  padding-bottom: 12px;
  text-align: left;
  background-color: var(--background3);
  color: var(--textColor);
}

.tableStyle input,
select,
option {
  width: 100%;
  height: 40px;
  border-radius: 5px;
  background-color: var(--background2);
  color: var(--textColor);
  border: 1px solid var(--background3);
  padding: 5px;
  box-sizing: border-box;

}

.navigationTableGroup {
  margin-top: 20px;
  text-align: right;
}

.navigationTableGroup p {
  display: inline;
  font-size: 15px;
  color: var(--textColor);
}

.navigationTableGroup input {
  width: 40px !important;
  height: 30px;
  border-radius: 5px !important;
  background-color: var(--background2) !important;
  color: var(--textColor) !important;
  border: 1px solid var(--accentMain) !important;
  padding: 5px;
  text-align: center;
}

.buttonFillNav {
  appearance: none;
  border: 0;
  padding: 5px 15px 5px 15px;
  height: 40px;
  color: #fff;
  background-color: var(--accentMain);
  border-radius: 5px;
  cursor: pointer;
  font-size: 15px;
}

.buttonFillNavWF {
  appearance: none;
  border: 0;
  padding: 5px 15px 5px 15px;
  height: 40px;
  color: var(--textColor);
  border: 1px solid var(--accentMain);
  background-color: transparent;
  border-radius: 5px;
  cursor: pointer;
  font-size: 15px;

}

.buttonFillNavWF:hover {
  background-color: var(--accentMainHover);
  box-shadow: var(--accentShadow);
}

.buttonFillNav:hover {
  background-color: var(--accentMainHover);
  box-shadow: var(--accentShadow);
}

.modalContainer {
  height: 100%;
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  /* background-color: rgba(0, 0, 0, 0.4); */
  z-index: 999;
  transform: scale(0);
  transition: 0.1s ease;
}

.modalContainer.active {
  transform: scale(1);
}

::-webkit-scrollbar {
  /* chrome based */
  width: 0px;
  /* ширина scrollbar'a */
  background: transparent;
  /* опционально */
}

html {
  -ms-overflow-style: none;
  /* IE 10+ */
  scrollbar-width: none;
  /* Firefox */
}

.modalContent {
  max-height: 80%;
  overflow-y: auto;
  padding: 20px;
  border-radius: 10px;
  background-color: var(--background3Glass);
  color: var(--textColor);
  backdrop-filter: blur(6px);
  -webkit-backdrop-filter: blur(9.2px);
  width: 1000px;
  -webkit-box-shadow: -1px 16px 100px 18px rgba(250, 156, 16, 0.1);
  -moz-box-shadow: -1px 16px 100px 18px rgba(250, 156, 16, 0.1);
  box-shadow: -1px 16px 100px 18px rgba(250, 156, 16, 0.1);
  max-width: 90% !important;
}

.modalContent input,
select,
option,
textarea {
  width: 100%;
  height: 40px;
  border-radius: 5px;
  background-color: var(--background2);
  color: var(--textColor);
  border: 1px solid var(--background3);
  padding: 5px;
  box-sizing: border-box;
}

.modalContent textarea {
  height: 150px;
}

.drag-and-drop {
  width: 100%;
  height: 150px;
  border-radius: 5px;
  background-color: var(--background2);
  color: var(--textColor);
  border: 2px dashed var(--background1);
  padding: 5px;
  box-sizing: border-box;
  top: 0;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}

.drag-and-drop p {
  font-size: 12px;
}

.drag-and-drop.hoverDrop {
  border: 2px dashed var(--accentMain);
  transition: 0.5s;
}


.mLabelForCheckbox {
  display: block;
  border: 1px solid var(--accentMain);
  border-radius: 5px;
  width: 20px;
  height: 20px;
  transition: background-color 0.3s ease;
  text-align: center;
}

.mCheckbox {
  display: none;
  text-align: center;
}

.mCheckbox:checked+label {
  background-color: var(--accentMain);
  border: 1px solid var(--background3);
}

.fileNameBlock {
  height: 40px;
  width: 100%;
  top: 0;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 1px solid var(--accentMain);
  z-index: 999;
}

.loader {
  width: 12px;
  height: 12px;
  border-radius: 50%;
  display: block;
  margin: 15px auto;
  position: relative;
  color: var(--accentMain);
  box-sizing: border-box;
  animation: animloader 1s linear infinite alternate;
}

.punctHeader {
  font-size: 20px;
  margin: 0px;
  display: inline-block;
  width: 88%;
  font-weight: 500;
  color: var(--accentMain);
}

.editPunctInput {
  font-size: 15px;
  margin: 0px;
  display: inline-block;
  width: 92% !important;
  font-weight: 500;
  color: var(--accentMain);
}

.punctList button {
  appearance: none;
  border: 0;
  height: 30px;
  color: var(--textColor);
  border: 1px solid var(--accentMain);
  background-color: transparent;
  border-radius: 5px;
  cursor: pointer;
  font-size: 15px;
  width: 3%;
  margin-left: 1%;
  color: var(--textColor);
}

.punctList hr {
  margin-top: 5px;
}

.hiddenHR1 {
  visibility: hidden;
}

.punctHeaderGroup:hover+.hiddenHR1 {
  visibility: visible;
}

.podpunctHeader {
  font-size: 15px;
  margin: 0px;
  display: inline-block;
  width: 92%;
}

.podpunctList button {
  appearance: none;
  border: 0;
  height: 30px;
  color: var(--textColor);
  background-color: transparent;
  border-radius: 5px;
  cursor: pointer;
  font-size: 15px;
  width: 3%;
  margin-left: 1%;
  color: var(--textColor);
}

.podpunctList hr {
  margin-top: 5px;
}

.hiddenHR2 {
  visibility: hidden;
}

.podpunctHeaderGroup:hover+.hiddenHR2 {
  visibility: visible;
}

.punctList button:hover {
  background-color: var(--accentMainHover);
  box-shadow: var(--accentShadow);
}

.podpunctList button:hover svg {
  color: var(--accentMain);
}

@keyframes animloader {
  0% {
    box-shadow: -38px -12px, -14px 0, 14px 0, 38px 0;
  }

  33% {
    box-shadow: -38px 0px, -14px -12px, 14px 0, 38px 0;
  }

  66% {
    box-shadow: -38px 0px, -14px 0, 14px -12px, 38px 0;
  }

  100% {
    box-shadow: -38px 0, -14px 0, 14px 0, 38px -12px;
  }
}


@media only screen and (min-width: 768px) and (max-width: 991px),
(max-width: 767px) {
  .sidebar {
    display: none;
  }

  .sidebar--open {
    display: block;
  }

  .content {
    margin-left: 0px;
    margin-right: 0px;
    width: 100%;
  }

  .mobW {
    margin-top: 10px !important;
    width: 100% !important;
  }

  .buttonFill {
    width: 100%;
  }

  .buttonWithoutFill {
    width: 100%;
  }

  .modalContent {
    width: 95%;
  }
}