@import url('https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500;600;700&display=swap');



.ll {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Quicksand', sans-serif;
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
    background: var(--background3);
}

.ll,
section {
    position: absolute;
    width: 100vw;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 2px;
    flex-wrap: wrap;
    overflow: hidden;
}

.ll section span {
    position: relative;
    display: block;
    width: calc(6.25vw - 2px);
    height: calc(6.25vw - 2px);
    background: var(--background2);
    z-index: 2;
    transition: 1.5s;
}

.ll section span:hover {
    background: var(--accentMain);
    transition: 0s;
}

.ll section .signin {
    position: absolute;
    width: 400px;
    background: var(--background3);
    z-index: 1000;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 40px;
    border-radius: 4px;
    box-shadow: 0 15px 35px var(--accentMain);
}

.licontent {
    color: #fff;
}

.licontent input {
    width: 390px;
    height: 50px;
    border-radius: 5px;
    background-color: var(--background2);
    color: var(--textColor);
    border: 1px solid var(--background3);
    padding: 5px;
    box-sizing: border-box;
}
.licontent button {
    width: 390px;
    height: 50px;
    margin-top: 20px;
}

@media (max-width: 900px) {
    .ll section span {
        width: calc(10vw - 2px);
        height: calc(10vw - 2px);
    }
}

@media (max-width: 600px) {
    .ll section span {
        width: calc(20vw - 2px);
        height: calc(20vw - 2px);
    }
    .ll section .signin {
        position: absolute;
        width: 250px;
        background: var(--background3);
        z-index: 1000;
        display: flex;
        justify-content: center;
        align-items: center;
        padding: 40px;
        border-radius: 4px;
        box-shadow: 0 15px 35px var(--accentMain);
    }
    .licontent input {
        width: 240px;
        height: 40px;
        border-radius: 5px;
        background-color: var(--background2);
        color: var(--textColor);
        border: 1px solid var(--background3);
        padding: 5px;
        box-sizing: border-box;
    }
    .licontent button {
        width: 240px;
    }
}